.rootCont {
    /* height: 90vh; */
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    /* padding-bottom: 10vh; */
    text-align: center;
    /* border: 5px solid rgb(196, 13, 233); */
  }
  
  .rootCont h1 {
    color: rgb(0, 49, 64);
    margin: 0 auto;
  }
  .rootCont > p {
    font-size: 30px;
    font-weight: 400;
    color: rgb(8, 111, 143);
    /* border: 5px solid rgb(18, 199, 235); */
    /* margin: 10; */
  }
  .rootCont > p:nth-of-type(1) {
    font-size: 40px;
    font-weight: 400;
    color: black;
    /* border: 5px solid green; */
    /* margin: 50px; */
  }
  .skillsContFront {
    display: grid;
    grid-template-columns: repeat(6, 16.6%);
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 50px;
    /* border: 2px solid rgb(11, 48, 234) */
    /* height: 50vh; */
  }
  .skillsContBack {
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 50px;
    /* border: 2px solid rgb(68, 215, 14) */
    /* height: 50vh; */
  }
  .toolsDiv {
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 50px;
    /* border: 2px solid rgb(223, 49, 78) */
    /* height: 50vh; */
  }
  .skillCard {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100px;
    margin: auto;
     /* border: 2px solid rgb(223, 49, 78);  */
  }
  .skillIcon {
    font-size: 40px;
    color: rgb(8, 111, 143);
    width: 80%;
    height: 90%;
    /* border: 2px solid rgb(223, 49, 78); */
  }
  .expSkillIcon {
    font-size: 31px;
    font-weight: 470;
    color: rgb(8, 111, 143);
  }
  .reactIcon:hover {
    color: rgb(94, 211, 243);
  }
  .reduxIcon:hover {
    color: rgb(118, 76, 183);
  }
  .jsIcon:hover {
    color: rgb(247, 223, 30);
  }
  .muiIcon:hover {
    color: rgb(0, 129, 203);
  }
  .htmlIcon:hover {
    color: rgb(233, 98, 40);
  }
  .cssIcon:hover {
    color: rgb(37, 75, 221);
  }
  .nodeIcon:hover {
    color: rgb(95, 151, 89);
  }
  .mongoIcon:hover {
    color: rgb(82, 167, 75);
  }
  .gitIcon:hover {
    color: rgb(232, 78, 49);
  }
  .expIcon {
    margin: 0;
  }
  .expIcon:hover {
    color: black;
  }
  @media only screen and (max-width: 820px) {
    /* .rootCont {
      height: 140vh;
      padding-bottom: 5vh;
    } */
    .rootCont {
     width: 90%;
     /* display: grid;
     grid-template-columns: repeat(1,1fr);
     border: 2px solid rgb(223, 49, 78) */
    }
    .skillsContFront {
      grid-template-columns: repeat(3, 33.3%);
      /* height: 120vh; */
    }
  }

  @media only screen and (max-width: 350px) {
    /* .rootCont {
      height: 140vh;
      padding-bottom: 5vh;
    } */
    .rootCont {
     width: 90%;
       display: grid;
     grid-template-columns: repeat(1,1fr);
     /* border: 2px solid rgb(208, 5, 215)   */
    }
    /* .skillsContFront {
      grid-template-columns: repeat(3, 33.3%);
      /* height: 120vh; */
    /* } */ 
    
    .skillsContFront {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      
      align-items: center;
      border-radius: 10px;
      margin-bottom: 50px;
       /* border: 2px solid rgb(11, 48, 234)   */
      /* height: 50vh; */
    }
    .skillsContBack {
      display: grid;
      grid-template-columns: repeat(2,1fr);
      justify-content: space-between;
      align-items: center;
      border-radius: 10px;
      margin-bottom: 50px;
      /* border: 2px solid rgb(68, 215, 14) */
      /* height: 50vh; */
    }
    .toolsDiv {
      display: grid;
      grid-template-columns: repeat(2,1fr);
      justify-content: space-between;
      align-items: center;
      border-radius: 10px;
      margin-bottom: 50px;
      /* border: 2px solid rgb(223, 49, 78) */
      /* height: 50vh; */
    }

    
  }